// d3Heatmap.js
import * as d3 from 'd3'

export default class HeatmapD3 {
    constructor(el, props) {
        const self = this;
        self.update = self.update.bind(self);
        self.wrappedClick = self.wrappedClick.bind(self);
        self.props = props

        var margin = {top: 30, right: 100, bottom: 50, left: 50},
            width = props.width - margin.left - margin.right,
            height = props.height - margin.top - margin.bottom;

        this.width = width;
        this.height = height;

        self.svg = d3.select(el).append('svg')
            .attr('width', width + margin.left + margin.right)
            .attr('height', height + margin.top + margin.bottom)
            .append('g')
            .attr('transform',
                'translate(' + margin.left + ',' + margin.top + ')');
        // Draw axes
        var cols = props.data.columns.sort(d3.ascending)
        var rows = props.data.rows.sort(d3.ascending)
        var scaleX = d3.scaleLinear()
            .domain([cols[0], cols[cols.length - 1]])
            .range([0, width])
        self.axX = self.svg.append('g')
            .style("font", props.fontSize + "px times")
            .attr('transform', 'translate(0,' + height + ')')
            .call(d3.axisBottom(scaleX));
        self.xLabel = self.axX.append('text')
            .attr('x', 0.5 * width)
            .attr('y', 0.8 * margin.bottom)
            .attr("text-anchor", "middle")
            .attr("fill", "black")
            .attr("font-size", props.fontSize)
            .text(props.xLabel);
        var scaleY = d3.scaleLinear()
            .domain([rows[0], rows[rows.length - 1]])
            .range([height, 0])
        self.axY = self.svg.append('g')
            .style("font", props.fontSize + "px times")
            .call(d3.axisLeft(scaleY));
        self.yLabel = self.axY.append('text')
            .attr("transform", "rotate(-90)")
            .attr('x', -0.5 * height)
            .attr('y', -0.8 * margin.bottom)
            .attr("text-anchor", "middle")
            .attr("fill", "black")
            .attr("font-size", props.fontSize)
            .text(props.yLabel);
        // Add tooltip
        var tooltip = d3.select(el)
            .append('div')
            .style("opacity", 0)
            .attr("class", "tooltip")
            .style("background-color", "white")
            .style("border", "solid")
            .style("border-width", "2px")
            .style("border-radius", "5px")
            .style("padding", "5px")
            .style("position", "absolute")
        // Three function that change the tooltip when user hover / move / leave a cell
        var mouseover = function (d) {
            tooltip.style("opacity", 1)
        }
        var mousemove = function (d, object) {
            tooltip
                .html(object.x + "," + object.y + "<br>Value: " + object.value)
                .style("left", (d.pageX + 10) + "px")
                .style("top", (d.pageY + 10) + "px");
        }
        var mouseleave = function (d) {
            tooltip.style("opacity", 0)
        }

        var minValue = props.data.values[0].value
        var maxValue = props.data.values[0].value
        props.data.values.forEach(e => {
            if (e.value < minValue) {
                minValue = e.value
            }
            if (e.value > maxValue) {
                maxValue = e.value
            }
        })

        // Add color scale
        var colorScale = d3.scaleSequential(d3.interpolateViridis)
            .domain([maxValue, minValue])

        // Color bar
        const legendProps = {
            title: props.valueLabel,
            tickSize: 6,
            width: 30,
            height: props.height - margin.top - margin.bottom,// + tickSize,
            marginTop: 0,
            marginRight: 0,
            marginBottom: 0,// + tickSize,
            marginLeft: 10,
            ticks: width / 64
        };

        function ramp(color, n = 256) {
            const canvas = document.createElement("canvas");
            canvas.width = 1;
            canvas.height = n;
            const context = canvas.getContext("2d");
            for (let i = 0; i < n; ++i) {
                context.fillStyle = color(i / (n - 1));
                context.fillRect(0, n - 1 - i, 1, 1);
            }
            return canvas;
        }

        const n = Math.min(colorScale.domain().length, colorScale.range().length)
        let x = colorScale.copy().rangeRound(d3.quantize(d3.interpolate(legendProps.marginTop, legendProps.height - legendProps.marginBottom), n));
        self.svg.append("image")
            .attr("x", width + legendProps.marginLeft)
            .attr("y", legendProps.marginTop)
            .attr("width", legendProps.width - legendProps.marginLeft - legendProps.marginRight)
            .attr("height", legendProps.height - legendProps.marginTop - legendProps.marginBottom)
            .attr("preserveAspectRatio", "none")
            .attr("xlink:href", ramp(colorScale.copy().domain(d3.quantize(d3.interpolate(0, 1), n))).toDataURL());
        self.legendTitle = self.svg.append("g")
            .style("font", props.fontSize + "px times")
            .attr("transform", 'translate(' + (width + legendProps.width) + ',0)')
            .call(d3.axisRight(x))
            .call(g => g.select(".domain").remove())
            .append("text")
            .attr("x", -legendProps.width + legendProps.marginLeft)
            .attr("y", -5)
            .attr("fill", "black")
            .attr("text-anchor", "start")
            .attr("font-size", props.fontSize)
            .text(legendProps.title);

        colorScale = d3.scaleSequential(d3.interpolateViridis)
            .domain([minValue, maxValue])

        // draw cells
        var cellSizeX = calculateSizes(cols);
        var cellSizeY = calculateSizes(rows);
        var unitSizeX = width / (cols[cols.length - 1] - cols[0]);
        var unitSizeY = height / (rows[rows.length - 1] - rows[0]);
        this.cells = self.svg.selectAll('rect')
            .data(props.data.values)
            .enter().append('g')
        this.cells.append('rect')
            .attr('class', 'cell')
            .attr('width', function (d) {
                return unitSizeX * (cellSizeX[d.x].left + cellSizeX[d.x].right)
            })
            .attr('height', function (d) {
                return unitSizeY * (cellSizeY[d.y].left + cellSizeY[d.y].right)
            })
            .attr('y', function (d) {
                return scaleY(d.y + cellSizeY[d.y].right)
            })
            .attr('x', function (d) {
                return scaleX(d.x - cellSizeX[d.x].left)
            })
            .attr('fill', function (d) {
                return colorScale(d.value)//"#FFFFFF10"//
            })
            .attr('value', function (d) {
                return d.value
            })
            .attr('dataID', function (d) {
                return d.id;
            })
            .on("mouseover", mouseover)
            .on("mousemove", mousemove)
            .on("mouseleave", mouseleave)
            .on("click", self.wrappedClick)
        if (props.showPoints) {
            this.cells.append("circle")
                .attr('cx', function (d) {
                    return scaleX(d.x)
                })
                .attr('cy', function (d) {
                    return scaleY(d.y)
                })
                .attr('r', 5)
                .style('fill', 'black')
        }

        var isBrushed = (brush_coords, cx, cy, w, h) => {
            var x0 = brush_coords[0][0],
                x1 = brush_coords[1][0],
                y0 = brush_coords[0][1],
                y1 = brush_coords[1][1]
            // selects if there is an intersection
            var bottomLeftX = Math.max(cx, x0),
                bottomLeftY = Math.max(cy, y0),
                topRightX = Math.min(cx + w, x1),
                topRightY = Math.min(cy + h, y1);
            return (bottomLeftX < topRightX && bottomLeftY < topRightY);
        };

        var updateBrush = (event) => {
            var extent = event.selection;
            this.cells.style("opacity", function (d) {
                var w = unitSizeX * (cellSizeX[d.x].left + cellSizeX[d.x].right);
                var h = unitSizeY * (cellSizeY[d.y].left + cellSizeY[d.y].right);
                if(isBrushed(extent, scaleX(d.x-cellSizeX[d.x].left), scaleY(d.y+cellSizeY[d.y].right), w, h)) {
                    return 1;
                }
                else {
                    return 0.5;
                }
            })
        }

        // Add brushing
        this.svg
            .call(d3.brush()
                .extent([[0, 0], [this.width, this.height]])
                .filter(event => event.ctrlKey)
                .on("start end", updateBrush))
    }

    wrappedClick(d, object) {
        this.props.setProps({selectedId: object.id});
        console.log("Click!");
    }

    update(props) {
        this.legendTitle
            .text(props.valueLabel);
    }
}

const calculateSizes = (a) => {
    var cellSize = {}
    cellSize[a[0]] = {
        left: 0,
        right: 0.5 * (a[1] - a[0])
    }
    for (let i = 1; i < a.length - 1; i++) {
        cellSize[a[i]] = {
            left: 0.5 * (a[i] - a[i - 1]),
            right: 0.5 * (a[i + 1] - a[i])
        }
    }
    cellSize[a[a.length - 1]] = {
        left: 0.5 * (a[a.length - 1] - a[a.length - 2]),
        right: 0,
    }
    return cellSize;
}
